import React, { useEffect, useState } from "react";

import MenuItem from "@mui/material/MenuItem";

import FormControl from "@mui/material/FormControl";

import Select from "@mui/material/Select";

import axios from "axios";

const FormData = require("form-data");

const data = new FormData();

export default function SelectRol({ roles, setId, getrole, setcolor }) {
  const [role, setrole] = useState("");

  console.log(setId);
  console.log(setcolor);

  const handleChange = (event) => {
    setrole(event.target.value);

    console.log(event.target.value);

    data.append("role_id", event.target.value);

    var options = {
      method: 'PUT',
      url: `http://api.machinedtg.com/users/${setId}`,
      
      headers: {
        Authorization: "Bearer " + localStorage.getItem("token"),
      },
      data: data
      
    };

    axios(options)
      .then((response) => {
        window.location.reload();
      })
      .catch((error) => {
        console.log(error);
        alert(error);
      });
  };


  return (
    <FormControl sx={{ m: 1, minWidth: 105 }}>
      <Select
        style={{ background: setcolor, height: 30, fontSize: "0.8em", borderRadius: "0.5rem" }}
        value={role}
        onChange={handleChange}
        displayEmpty
        inputProps={{ "aria-label": "Without label" }}
      >
        <MenuItem value="">{roles}</MenuItem>
        {getrole.map((c) => (
          <MenuItem key={c.id} value={c.id}>
            {c.name}
          </MenuItem>
        ))}
      </Select>
    </FormControl>
  );
}
