import React, { useState } from "react";

import { Box, Button, Grid, Typography } from "@mui/material";
import LocalPrintshopOutlinedIcon from "@mui/icons-material/LocalPrintshopOutlined";

import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import barcode from "./barcode.png";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import CheckCircleIcon from "@mui/icons-material/CheckCircle";
import Tshert from "../img/Tshert.png";

function createData(name, calories, fat, carbs, protein) {
  return { name, calories, fat, carbs, protein };
}

const rows = [createData(1234567, "Black", "XL", "F", <img src={Tshert} style={{ width: "10%", height: "10%" }} alt="Tshert" />)];

export default function OrderScan() {
  const [open, setOpen] = useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div style={{ marginLeft: "2rem" }}>
      <Grid container spacing={2} style={{ marginBottom: "1rem" }}>
        <Grid item xs={3} md={3}>
          <Typography>Barcode and Scan</Typography>
        </Grid>
      </Grid>
      <Grid container spacing={2}>
        <Grid item xs={3} md={3}>
          <img src={barcode} style={{ width: "140%", marginLeft: "0em", marginTop: "1em" }} alt="barcode" />
        </Grid>

        <Grid item xs={3} md={3}>
          <img src={Tshert} style={{ width: "110%", zIndex: 50, marginLeft: "27em" }} alt="Tshert" />
        </Grid>
      </Grid>
      <Grid container spacing={2} style={{ marginBottom: "6rem", marginTop: "-11em" }}>
        <Grid item xs={3} md={3}>
          <Box sx={{ width: "152px", height: "48px", border: "thin solid gray" }}>
            <Typography sx={{ margin: "12px" }}> 1322526</Typography>
          </Box>
        </Grid>
        <Grid item xs={3} md={3}>
          <div>
            <Button variant="contained" onClick={handleClickOpen} style={{ marginTop: "0.5em", marginLeft: "70%", background: "rgba(175, 185, 255, 1)" }}>
              <LocalPrintshopOutlinedIcon sx={{ marginRight: "0.8em" }} /> print
            </Button>
            <Dialog open={open} onClose={handleClose} aria-labelledby="alert-dialog-title" aria-describedby="alert-dialog-description">
              <DialogTitle id="alert-dialog-title" sx={{ textAlign: "center" }}>
                {<CheckCircleIcon color="success" sx={{ fontSize: 60 }} />}
              </DialogTitle>
              <DialogContent sx={{ width: "331px" }}>
                <DialogContentText
                  id="alert-dialog-description"
                  sx={{ textAlign: "center", fontSize: "24px", fontWeight: 700, color: "rgba(124, 134, 145, 1)" }}
                >
                  Success
                </DialogContentText>
              </DialogContent>
              <DialogActions>
                <Button variant="contained" color="success" sx={{ width: "144px", marginBottom: "20px" }} onClick={handleClose} autoFocus>
                  Done
                </Button>
              </DialogActions>
            </Dialog>
          </div>
        </Grid>
      </Grid>
      <Grid container spacing={2} style={{ marginBottom: "1.5rem", marginTop: "0.5rem" }}>
        <Grid item xs={3} md={3}>
          <Typography> barcod:1322526</Typography>
        </Grid>
      </Grid>
      <Grid container spacing={2} style={{ marginBottom: "1.5rem", marginTop: "0.5rem", marginLeft: "-2.5em" }}>
        <Grid item xs={10} md={10}>
          <TableContainer component={Paper}>
            <Table sx={{ minWidth: 700 }} aria-label="simple table">
              <TableHead>
                <TableRow>
                  <TableCell sx={{ width: 100 }}>Product Id</TableCell>
                  <TableCell align="right">Color</TableCell>
                  <TableCell align="right">Size</TableCell>
                  <TableCell align="right">F/B</TableCell>
                  <TableCell align="center">Preview</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {rows.map((row) => (
                  <TableRow
                    key={row.name}
                    sx={{
                      "&:last-child td, &:last-child th": { border: 0 },
                    }}
                  >
                    <TableCell component="th" scope="row">
                      {row.name}
                    </TableCell>
                    <TableCell align="right">{row.calories}</TableCell>
                    <TableCell align="right">{row.fat}</TableCell>
                    <TableCell align="right">{row.carbs}</TableCell>
                    <TableCell align="center" style={{ width: 330 }}>
                      {row.protein}
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </Grid>
      </Grid>
      <Grid container spacing={2} style={{ marginBottom: "0.5rem", marginTop: "0.2rem" }}>
        <Grid item xs={4} md={4}>
          <Button variant="contained" color="primary" sx={{ width: "11em" }}>
            {" "}
            scan it
          </Button>
        </Grid>
      </Grid>
    </div>
  );
}
