import * as React from "react";
import "../Pretreat/Pretreat.css";
import "antd/dist/antd";
import "antd/dist/antd.css";
import Spinner from "../Spinner";

import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell, { tableCellClasses } from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import { styled } from "@mui/material/styles";
import TableRow from "@mui/material/TableRow";
import PropTypes from 'prop-types';
import Paper from "@mui/material/Paper";
import { useTheme } from '@mui/material/styles';
import Fab from "@mui/material/Fab";
import AddIcon from "@mui/icons-material/Add";
import { NavLink } from "react-router-dom";
import { useEffect } from "react";
import axios from "axios";
import { useState } from "react";
import EditButton from "./EditButton";
import { Box, Grid, IconButton, TablePagination } from "@mui/material";
import { KeyboardArrowLeft, KeyboardArrowRight } from "@mui/icons-material";
import FirstPageIcon from '@mui/icons-material/FirstPage';
import LastPageIcon from '@mui/icons-material/LastPage';


function TablePaginationActions(props) {
  const theme = useTheme();
  const { count, page, rowsPerPage, onPageChange } = props;



  const handleBackButtonClick = (event) => {
    onPageChange(event, page - 1);
  };

  const handleNextButtonClick = (event) => {
    onPageChange(event, page + 1);
  };


  // var config = {
  //   method: "GET",
  //   url: `http://api.machinedtg.com/products/page/${page}`,
  //   headers: {
  //     Authorization: "Bearer " + localStorage.getItem("token"),
  //   },
  // };
  // axios(config)
  //   .then((response) => {
  //     let addpro = response.data.products;
      
  //     console.log(addpro);
  //     alert("ok");
  //     // console.log(response);
  //   })
  //   .catch((error, response) => {
  //     // console.log(response);
  //     // console.log(error);
  //     alert(`${error} NextButtonClick `);
  //   });

  return (
    <Box sx={{ flexShrink: 0, ml: 2.5 }}>
   
      <IconButton
        onClick={handleBackButtonClick}
        disabled={page === 0}
        aria-label="previous page"
      >
        {theme.direction === 'rtl' ? <KeyboardArrowRight /> : <KeyboardArrowLeft />}
      </IconButton>
      <IconButton
        onClick={handleNextButtonClick}
        disabled={page >= Math.ceil(count / rowsPerPage) - 1}
        aria-label="next page"
      >
        {theme.direction === 'rtl' ? <KeyboardArrowLeft /> : <KeyboardArrowRight />}
      </IconButton>
     
    </Box>
  );
}


TablePaginationActions.propTypes = {
  count: PropTypes.number.isRequired,
  onPageChange: PropTypes.func,
  page: PropTypes.number.isRequired,
  rowsPerPage: PropTypes.number.isRequired,
};

export default function ProductMangment() {
  const [rows1, setrows1] = useState([]);
  const [page, setPage] = React.useState(1);
  const [loading, Setloading] = useState(false);
  
  useEffect(() => {
    Setloading(true);
    var config = {
      method: "GET",
      url: `http://api.machinedtg.com/products/page/1`,
      headers: {
        Authorization: "Bearer " + localStorage.getItem("token"),
      },
    };
    axios(config)
      .then((response) => {
        let addpro = response.data.products;
        setrows1(addpro);
        // console.log(addpro);
        Setloading(false);
        // console.log(response);
      })
      .catch((error, response) => {
        // console.log(response);
        // console.log(error);
        Setloading(false);
        alert(`${error}table product `);
      });
  }, []);

  const StyledTableCell = styled(TableCell)(({ theme }) => ({
    [`&.${tableCellClasses.head}`]: {
      backgroundColor: theme.palette.common.black,
      color: theme.palette.common.white,
    },
    [`&.${tableCellClasses.body}`]: {
      fontSize: 14,
    },
  }));

  const StyledTableRow = styled(TableRow)(({ theme }) => ({
    "&:nth-of-type(odd)": {
      backgroundColor: theme.palette.action.hover,
    },
    // hide last border
    "&:last-child td, &:last-child th": {
      border: 0,
    },
  }));
  const handleChangePage = (event, newPage) => {
    var config = {
      method: "GET",
      url: `http://api.machinedtg.com/products/page/${page}`,
      headers: {
        Authorization: "Bearer " + localStorage.getItem("token"),
      },
    };
    axios(config)
      .then((response) => {
        let addpro = response.data.products;
        setPage(page+1);
        setrows1(addpro);
        console.log(addpro);
        alert("ok");
        // console.log(response);
      })
      .catch((error, response) => {
        // console.log(response);
        // console.log(error);
        alert(`${error}table ChangePage `);
      });
    
  };

  return (
    <Grid item md={12} lg={12} sx={{ flexGrow: 1, margin: "2em 0" }}>
    {loading ? (
      <Spinner />
    ) : (
      <>
      
   
      <TableContainer component={Paper}>
        <Table sx={{ minWidth: 750 }} aria-label="customized table">
          <TableHead>
            <TableRow>
              <StyledTableCell style={{ minWidth: 150 }}>Product name </StyledTableCell>
              <StyledTableCell>SKU </StyledTableCell>
              <StyledTableCell>color</StyledTableCell>
              <StyledTableCell>Size </StyledTableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {rows1.map((row) => (
              <StyledTableRow key={row.id}>
                <StyledTableCell component="th" scope="row">
                  {row.name}
                </StyledTableCell>
                <StyledTableCell>{row.sku}</StyledTableCell>
                <StyledTableCell>{row.color}</StyledTableCell>
                <StyledTableCell>{row.size}</StyledTableCell>
                <StyledTableCell>
                  <EditButton setId={row.id} setsku={row.sku} setName={row.name} setColor={row.color} setSize={row.size} />
                </StyledTableCell>
              </StyledTableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>

      <TablePagination
        rowsPerPageOptions={[7]}
        component="div"
        count={rows1.length}
        
        page={page}
        ActionsComponent={TablePaginationActions}
        
        />

</>
        )}
      <Grid item xs={12} md={12} sx={{ display: "flex", justifyContent: "end", marginRight: "3.4em", marginTop: "2em" }}>
        <NavLink to="/user/addprodut/Add-Product">
          <Fab color="primary" aria-label="add">
            <AddIcon />
          </Fab>
        </NavLink>
      </Grid>
    </Grid>
  );
}
