import React, { useEffect, useState } from "react";
import { Form, Row, Col, Button, Container, Nav, Image } from "react-bootstrap";
import "./StyleLog.css";
import { ToastContainer, toast } from "react-toastify";
import { loginUser } from "../../services/userservice.js";
import imglog from "./imglog.png";
import { useNavigate } from "react-router-dom";
import OutlinedInput from "@mui/material/OutlinedInput";
import InputLabel from "@mui/material/InputLabel";
import InputAdornment from "@mui/material/InputAdornment";
import Visibility from "@mui/icons-material/Visibility";
import VisibilityOff from "@mui/icons-material/VisibilityOff";
import FormControl from "@mui/material/FormControl";
import IconButton from "@mui/material/IconButton";

function Login(props) {
  useEffect(() => {
    document.title = "dtg | login";
  }, []);

  const [values, setValues] = React.useState({
    showPassword: false,
  });

  const handleClickShowPassword = () => {
    setValues({
      ...values,
      showPassword: !values.showPassword,
    });
  };

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };

  const [email, setemail] = useState("");
  const [password, setpassword] = useState("");

  const reset = () => {
    setemail("");
    setpassword("");
  };
  let navigate = useNavigate();
  const validateLogin = (users) => {
    if (!users.email) return toast.error("empty email");
    if (!users.password) return toast.error("empty password");
  };

  const handlesubmit = async (event, data) => {
    event.preventDefault();

    const users = {
      email: email,
      password: password,
    };
    validateLogin(users);
    try {
      const { status, data } = await loginUser(users);

      if (status === 200 && data.user.is_admin === 1) {
        localStorage.setItem("is_admin", data.user.is_admin);
        localStorage.setItem("token", data.user.token);
        localStorage.setItem("id", data.user.id);
        localStorage.setItem("name", data.user.name);
        localStorage.setItem("email", data.user.email);
        localStorage.setItem("profile_pic", data.user.profile_pic);
        localStorage.setItem("role", data.user.role_name);
        localStorage.setItem("is_disabled", data.user.is_disabled);
        navigate("/admin", { replace: true });
        toast.success("welcome");
      }
      if (status === 200 && data.user.is_admin === 0) {
        localStorage.setItem("is_admin", data.user.is_admin);
        localStorage.setItem("token", data.user.token);
        localStorage.setItem("id", data.user.id);
        localStorage.setItem("name", data.user.name);
        localStorage.setItem("email", data.user.email);
        localStorage.setItem("profile_pic", data.user.profile_pic);
        localStorage.setItem("role", data.user.role_name);
        localStorage.setItem("is_disabled", data.user.is_disabled);
        navigate("/user", { replace: true });
        toast.success("welcome");
      }

      localStorage.setItem("id", data.user.id);
      localStorage.setItem("name", data.user.name);
      localStorage.setItem("email", data.user.email);
      localStorage.setItem("profile_pic", data.user.profile_pic);
      localStorage.setItem("role", data.user.role_name);
      localStorage.setItem("is_disabled", data.user.is_disabled);

      reset();
    } catch (ex) {
      toast.error("not sent");
      console.log(ex);
    }
  };

  return (
    <Container fluid>
      <Row>
        {/* left page */}

        <Col md={6} className="Left">
          <h4 className="danger">DTG</h4>
          <h2 className="titlelog">LOGIN</h2>
          <p className="mt-5 mb-5 pwelcom">Welcome back! Please login to your account.</p>

          <Form onSubmit={handlesubmit}>
            {/* input email*/}

            <Form.Group as={Row} className="mb-4" controlId="formHorizontalEmail">
              <Col>
                <FormControl
                  sx={{
                    marginLeft: "0",
                    width: "82%",
                    marginTop: "1.5rem",
                    background: "white",
                  }}
                  variant="outlined"
                >
                  <InputLabel htmlFor="outlined-adornment-email">email</InputLabel>
                  <OutlinedInput id="outlined-adornment-email" type="email" value={email} onChange={(e) => setemail(e.target.value)} label="email" />
                </FormControl>
              </Col>
            </Form.Group>

            {/* input password */}

            <Form.Group as={Row} className="mb-3" controlId="formHorizontalPassword">
              <Col>
                <FormControl
                  sx={{
                    marginLeft: "0",
                    width: "82%",
                    marginTop: "1.5rem",
                    marginBottom: "1em",
                    background: "white",
                  }}
                  variant="outlined"
                >
                  <InputLabel htmlFor="outlined-adornment-password">Password</InputLabel>
                  <OutlinedInput
                    id="outlined-adornment-password"
                    type={values.showPassword ? "text" : "password"}
                    value={password}
                    onChange={(e) => setpassword(e.target.value)}
                    endAdornment={
                      <InputAdornment position="end">
                        <IconButton aria-label="toggle password visibility" onClick={handleClickShowPassword} onMouseDown={handleMouseDownPassword} edge="end">
                          {values.showPassword ? <VisibilityOff /> : <Visibility />}
                        </IconButton>
                      </InputAdornment>
                    }
                    label="Password"
                  />
                </FormControl>
              </Col>
            </Form.Group>

            <Form.Group as={Row} className="mb-5" controlId="formHorizontalCheck">
              <Col sm={{ span: 7 }}>
                <Form.Check label="Remember me" />
              </Col>
            </Form.Group>

            <Form.Group as={Row} className="mb-5">
              <Col sm={{ span: 10 }} className="d-grid gap-2">
                <Button type="submit">Login</Button>
              </Col>
            </Form.Group>
          </Form>
        </Col>

        {/* right page */}

        <Col md={6} className="Right">
          <Nav variant="pills" defaultActiveKey="#home" as="ul" className="nav">
            <Nav.Item as="li" className="itemHome">
              <Nav.Link href="#home">Home</Nav.Link>
            </Nav.Item>
            <Nav.Item as="li">
              <Nav.Link href="#shopify" className="itemShopify">
                shopify
              </Nav.Link>
            </Nav.Item>
          </Nav>

          <Image src={imglog} alt="logo" rounded style={{ width: "85%" }} />
        </Col>
      </Row>

      <ToastContainer />
    </Container>
  );
}

export default Login;
