import * as React from "react";
import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";

import DialogTitle from "@mui/material/DialogTitle";
import useMediaQuery from "@mui/material/useMediaQuery";
import { useTheme } from "@mui/material/styles";
import { Link } from "react-router-dom";

export default function ScanBtn() {
	const [open, setOpen] = React.useState(false);
	const theme = useTheme();
	const fullScreen = useMediaQuery(theme.breakpoints.down("md"));

	const handleClickOpen = () => {
		setOpen(true);
	};

	const handleClose = () => {
		setOpen(false);
	};

	return (
		<div >
			<Button  variant="contained" color="success"  style={{  marginLeft: "3rem" }} onClick={handleClickOpen}>
				Scan it
			</Button>
			<Dialog
				fullScreen={fullScreen}
				open={open}
				onClose={handleClose}
				aria-labelledby="responsive-dialog-title"
			
				
			>
				<DialogTitle id="responsive-dialog-title"  color="rgba(97, 98, 100, 1)" sx={{textAlign:"center",paddingTop:"30px",fontSize:"24px",fontWeight:700}}>
					{"Every thing is ok ?"}
				</DialogTitle>
		
				<DialogActions sx={{display:"flex",justifyContent:"center",alignItems:"center",textAlign:"center",width:"500px",paddingBottom:"50px"}}>
					<Button autoFocus onClick={handleClose}  variant="contained" style={{background:"rgba(37, 184, 69, 0.5)",color:"black",width:"124px",height:"44px"}}>
						yes
					</Button>
					
                    <Link  to="/user/qc/Qcproblem" style={{color:"white",marginLeft:"3rem",marginRight:"0.8rem"}}>
                    <Button autoFocus   variant="contained" style={{background:"rgba(224, 31, 61, 0.5)",color:"black",width:"124px",height:"44px"}}>
                    No
                    </Button>
					</Link>
					
				
				</DialogActions>
			</Dialog>
		</div>
	);
}
