import React from "react";
import "./Qc.css";
import ChooseProblem from "./ChooseProblem";
import TextareaAutosize from "@mui/material/TextareaAutosize";
import { Box, Button,Typography } from "@mui/material";

import Uploadpicture from "./Uploadpicture";

export default function Qcproblem() {
	return (
		<div style={{ marginTop: "1em" }}>
			<Typography style={{ margin: "1rem 0 0 0" }}>
				Choose problem
			</Typography>
			<ChooseProblem />
			<Typography style={{ margin: "1rem 0 0 0" }}>
				More details
			</Typography>
			<TextareaAutosize
				aria-label="minimum height"
				minRows={4}
				placeholder="Type here"
				style={{ width: "50rem" }}
			/>
			<Typography style={{ margin: "2rem 0 0 0" }}>
				Add picture
			</Typography>
			<Box  sx={{ p: 2, border: "1px solid grey",width: "50rem" }}>
				<Uploadpicture/>
			</Box>

			<Button
				variant="contained"
				type="submit"
				style={{ margin: "2rem 0" }}
				color="success"
			>
				submit
			</Button>
		</div>
	);
}
