import * as React from "react";
import Box from "@mui/material/Box";
import BottomNavigation from "@mui/material/BottomNavigation";
import BottomNavigationAction from "@mui/material/BottomNavigationAction";
import { Typography } from "@mui/material";
import "./Pretreat.css";
import { useEffect } from "react";
import axios from "axios";

export default function MachineBtn() {
  const [value, setValue] = React.useState(0);
  const [machine, setmachine] = React.useState([]);

  useEffect(() => {
    axios
      .get("http://localhost:4000/pretreat-machines/")
      .then((response) => {
        console.log(response);
        const machinelist = response.data.machines;
        console.log(machinelist);
        setmachine(machinelist);
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  return (
    <React.Fragment>
      <Typography sx={{ marginTop: "2em", marginLeft: "1em" }}>Which machine ?</Typography>
      <Box sx={{ width: "40em", border: "1px solid rgba(197, 199, 205, 1)", marginTop: "1em", marginLeft: "1em", marginBottom: "1.5em",padding:"1em" }}>
        <BottomNavigation
          showLabels
          value={value}
          onChange={(event, newValue) => {
            setValue(newValue);
          }}
        >
          {machine.map((machin) => (
            <BottomNavigationAction label={machin.name} key={machin.id} sx={{ p: 2, border: "1px solid rgba(197, 199, 205, 1)",marginRight:"1em" }} />
          ))}
        </BottomNavigation>
      </Box>
    </React.Fragment>
  );
}
