import React from "react";
import img from './img.png'
import useStyles from "./StyleHeader2";

import Button from "@mui/material/Button";
import Menu from "@mui/material/Menu";
import MenuItem from "@mui/material/MenuItem";

import NotificationsNoneOutlinedIcon from "@mui/icons-material/NotificationsNoneOutlined";
import SearchIcon from "@mui/icons-material/Search";
import { useParams } from "react-router-dom";
import Grid from "@mui/material/Grid";
import { Typography } from "@mui/material";
import { useNavigate } from "react-router-dom";


export default function Header2(props) {
	const params = useParams();
	const classes = useStyles();

	const [anchorEl, setAnchorEl] = React.useState(null);
	const open = Boolean(anchorEl);
	const handleClick = (event) => {
		setAnchorEl(event.currentTarget);
	};
	const handleClose = () => {
		setAnchorEl(null);
	};
	let navigate = useNavigate();
	const handleClose3 = () => {
		setAnchorEl(null);
		localStorage.clear()
		navigate("/", { replace: true });
		window.location.reload()
	};

	return (
		<div className={classes.header}>
			<Grid container spacing={2}>
				<Grid item>
					<Button
						className={classes.btn}
						id="demo-positioned-button"
						aria-controls="demo-positioned-menu"
						aria-haspopup="true"
						aria-expanded={open ? "true" : undefined}
						onClick={handleClick}
					>
						<img
							src={img}
							className={classes.img}
							alt="avatar"
						/>
					</Button>
					<Menu
						id="demo-positioned-menu"
						aria-labelledby="demo-positioned-button"
						anchorEl={anchorEl}
						open={open}
						onClose={handleClose}
						anchorOrigin={{
							vertical: "top",
							horizontal: "left",
						}}
						transformOrigin={{
							vertical: "top",
							horizontal: "left",
						}}
					>
						<MenuItem onClick={handleClose}>Profile</MenuItem>
						<MenuItem onClick={handleClose}>My account</MenuItem>
						<MenuItem onClick={handleClose3}>Logout</MenuItem>
					</Menu>
				</Grid>
				<Grid item>
					<Typography variant="p" style={{ fontSize: "1rem" }}>
					{localStorage.getItem("name")}
					</Typography>
					<br />
					<Typography
						variant="p"
						style={{ fontSize: "0.9rem", color:"rgba(197, 199, 205, 1)" }}
					>
						User
					</Typography>
				</Grid>
				<Grid item>
					<Button sx={{color:"rgba(197, 199, 205, 1)"}}>
						<NotificationsNoneOutlinedIcon />
					</Button>
				</Grid>
				<Grid item>
					<Button sx={{color:"rgba(197, 199, 205, 1)",marginRight:"-2.4em",marginTop:"0.2em"}} >
						<SearchIcon />
					</Button>
				</Grid>
				<Grid item >
					<Typography sx={{fontSize:"2em",position:"absolute",left:"12em"}}>
					{params.id}
					</Typography>
				</Grid>
			</Grid>
		</div>
	);
}
