import React, { useState } from "react";
import useStyles from "./Stylemenunew";



import { NavLink } from "react-router-dom";
import Typography from "@mui/material/Typography";
import Grid from "@mui/material/Grid";
import logo from "./logo.png";

import AssignmentReturnIcon from "@mui/icons-material/AssignmentReturn";
import ColorLensIcon from "@mui/icons-material/ColorLens";
import ImagesearchRollerIcon from "@mui/icons-material/ImagesearchRoller";
import GppGoodIcon from "@mui/icons-material/GppGood";
import DashboardIcon from "@mui/icons-material/Dashboard";
import { Button } from "@mui/material";
import LogoutIcon from "@mui/icons-material/Logout";
import { useNavigate } from "react-router-dom";

export default function Menunew() {
  const [anchorEl, setAnchorEl] = React.useState(null);
  let navigate = useNavigate();
  const handleClose = () => {
    setAnchorEl(null);
    localStorage.clear();
    navigate("/", { replace: true });
    window.location.reload();
  };

  const classes = useStyles();

  const [bgColour, setBgColour] = useState("#363740");
  const [borderleft, setborderleft] = useState("");
  const appStyles = {
    height: "40px",
    width: "100%",
    background: `${bgColour}`,
    borderLeft: `${borderleft}`,
  };

  const [bgColour1, setBgColour1] = useState("#363740");
  const [borderleft1, setborderleft1] = useState("");
  const appStyles1 = {
    height: "40px",
    width: "100%",
    background: `${bgColour1}`,
    borderLeft: `${borderleft1}`,
  };

  const [bgColour2, setBgColour2] = useState("#363740");
  const [borderleft2, setborderleft2] = useState("");
  const appStyles2 = {
    height: "40px",
    width: "100%",
    background: `${bgColour2}`,
    borderLeft: `${borderleft2}`,
  };
  const [bgColour3, setBgColour3] = useState("#363740");
  const [borderleft3, setborderleft3] = useState("");
  const appStyles3 = {
    height: "40px",
    width: "100%",
    background: `${bgColour3}`,
    borderLeft: `${borderleft3}`,
  };

  const [bgColour4, setBgColour4] = useState("#363740");
  const [borderleft4, setborderleft4] = useState("");
  const appStyles4 = {
    height: "40px",
    width: "100%",
    background: `${bgColour4}`,
    borderLeft: `${borderleft4}`,
  };

  const [bgColour5, setBgColour5] = useState("#363740");
  const [borderleft5, setborderleft5] = useState("");
  const appStyles5 = {
    height: "40px",
    width: "100%",
    background: `${bgColour5}`,
    borderLeft: `${borderleft5}`,
  };
  const [bgColour6, setBgColour6] = useState("#363740");
  const [borderleft6, setborderleft6] = useState("");
  const appStyles6 = {
    height: "40px",
    width: "100%",
    position: "absolute",
    bottom: "1em",
    display: "flex",
    alignItems: "center",
    background: `${bgColour6}`,
    borderLeft: `${borderleft6}`,
  };

  return (
    <div className={classes.menu}>
      <Grid container ditection={"row"} alignItems={"center"} mt={7}>
        <Grid item>
          <NavLink to={"orderintake/Order in take"}>
            <img src={logo} className={classes.logoicon} alt="logo" />
          </NavLink>
        </Grid>
        <Grid item>
          <NavLink to={"orderintake/Order in take"}>
            <Typography variant="h6" className={classes.logotext}>
              DTG auto system
            </Typography>
          </NavLink>
        </Grid>
      </Grid>

      <Grid container alignItems={"center"} className={classes.itemmenu} mt={7}>
        <Grid
          container
          item
          mb={4}
          className={classes.itemlink}
          style={appStyles}
          onMouseEnter={() => {
            setBgColour("rgb(159, 162, 180,0.08)");
            setborderleft("2px solid white");
          }}
          onMouseLeave={() => {
            setBgColour("#363740");
            setborderleft("");
          }}
        >
          <Grid item alignItems={"center"}>
            <NavLink to="orderintake/Order-in-take">
              <AssignmentReturnIcon className={classes.icon} />
            </NavLink>
          </Grid>

          <Grid item>
            <NavLink
              to="orderintake/Order-in-take"
              className={classes.itemtext}
              style={({ isActive }) => ({ color: isActive ? "#D9D9D9" : "rgb(159,162,180,0.40)" })}
            >
              Order in take
            </NavLink>
          </Grid>
        </Grid>

        <Grid
          container
          item
          mb={3}
          className={classes.itemlink}
          style={appStyles1}
          onMouseEnter={() => {
            setBgColour1("rgb(159, 162, 180,0.08)");
            setborderleft1("2px solid white");
          }}
          onMouseLeave={() => {
            setBgColour1("#363740");
            setborderleft1("");
          }}
        >
          <Grid item alignItems={"center"}>
            <NavLink to={"pretreat/Pretreat"}>
              <ColorLensIcon className={classes.icon} />
            </NavLink>
          </Grid>
          <Grid item>
            <NavLink
              to={"pretreat/Pretreat"}
              className={classes.itemtext}
              style={({ isActive }) => ({ color: isActive ? "#D9D9D9" : "rgb(159,162,180,0.40)" })}
            >
              Pretreat
            </NavLink>
          </Grid>
        </Grid>

        <Grid
          container
          item
          mb={3}
          className={classes.itemlink}
          style={appStyles2}
          onMouseEnter={() => {
            setBgColour2("rgb(159, 162, 180,0.08)");
            setborderleft2("2px solid white");
          }}
          onMouseLeave={() => {
            setBgColour2("#363740");
            setborderleft2("");
          }}
        >
          <Grid item alignItems={"center"}>
            <NavLink to={"dtg/DTG"}>
              <ImagesearchRollerIcon className={classes.icon} />
            </NavLink>
          </Grid>
          <Grid item>
            <NavLink to={"dtg/DTG"} className={classes.itemtext} style={({ isActive }) => ({ color: isActive ? "#D9D9D9" : "rgb(159,162,180,0.40)" })}>
              DTG
            </NavLink>
          </Grid>
        </Grid>

        <Grid
          container
          item
          mb={3}
          className={classes.itemlink}
          style={appStyles3}
          onMouseEnter={() => {
            setBgColour3("rgb(159, 162, 180,0.08)");
            setborderleft3("2px solid white");
          }}
          onMouseLeave={() => {
            setBgColour3("#363740");
            setborderleft3("");
          }}
        >
          <Grid item alignItems={"center"}>
            <NavLink to={"qc/QC"}>
              <GppGoodIcon className={classes.icon} />
            </NavLink>
          </Grid>
          <Grid item>
            <NavLink to={"qc/QC"} className={classes.itemtext} style={({ isActive }) => ({ color: isActive ? "#D9D9D9" : "rgb(159,162,180,0.40)" })}>
              QC
            </NavLink>
          </Grid>
        </Grid>

        <Grid
          container
          item
          mb={3}
          className={classes.itemlink}
          style={appStyles4}
          onMouseEnter={() => {
            setBgColour4("rgb(159, 162, 180,0.08)");
            setborderleft4("2px solid white");
          }}
          onMouseLeave={() => {
            setBgColour4("#363740");
            setborderleft4("");
          }}
        >
          <Grid item alignItems={"center"}>
            <NavLink to={"productmangment/Product-Mangment"}>
              <DashboardIcon className={classes.icon} />
            </NavLink>
          </Grid>
          <Grid item>
            <NavLink
              to={"productmangment/Product-Mangment"}
              className={classes.itemtext2}
              style={({ isActive }) => ({ color: isActive ? "#D9D9D9" : "rgb(159,162,180,0.40)" })}
            >
              Product mangement
            </NavLink>
          </Grid>
        </Grid>

        <Grid
          container
          item
          mb={3}
          className={classes.itemlink}
          style={appStyles5}
          onMouseEnter={() => {
            setBgColour5("rgb(159, 162, 180,0.08)");
            setborderleft5("2px solid white");
          }}
          onMouseLeave={() => {
            setBgColour5("#363740");
            setborderleft5("");
          }}
        >
          <Grid item alignItems={"center"}>
            <NavLink to={"shiping/Shiping"}>
              <DashboardIcon className={classes.icon} />
            </NavLink>
          </Grid>
          <Grid item>
            <NavLink to={"shiping/Shiping"} className={classes.itemtext} style={({ isActive }) => ({ color: isActive ? "#D9D9D9" : "rgb(159,162,180,0.40)" })}>
              Shiping
            </NavLink>
          </Grid>
        </Grid>
      </Grid>

      <Grid
        container
        item
        mb={3}
        className={classes.itemlink}
        style={appStyles6}
        onMouseEnter={() => {
          setBgColour6("rgb(159, 162, 180,0.08)");
          setborderleft6("2px solid white");
        }}
        onMouseLeave={() => {
          setBgColour6("#363740");
          setborderleft6("");
        }}
      >
        <Button onClick={handleClose}>
          <Grid item alignItems={"center"}>
            <NavLink to={"/"}>
              <LogoutIcon className={classes.iconlogout} />
            </NavLink>
          </Grid>
          <Grid item>
            <NavLink to="/" className={classes.itemtextlogout} style={({ isActive }) => ({ color: isActive ? "#D9D9D9" : "rgb(159,162,180,0.40)" })}>
              Log out
            </NavLink>
          </Grid>
        </Button>
      </Grid>
    </div>
  );
}
