import { Routes, Route } from "react-router-dom";
import { Navigate } from "react-router-dom";

import "./index.css";

import PageMain from "./component/Admin/Layuot/PageMain.jsx";
import PagemainEmployer from "./component/Employer/Layuot/PagemainEmployer.jsx";
import PrivateRoutesadmin from "./component/login/PrivateRoutesadmin.js";
import Overview from "./component/Admin/overview/Overview.jsx";
import Users from "./component/Admin/Users/User.jsx";
import Pretreat from "./component/Admin/Pretreat/Pretreat.jsx";
import Addusers from "./component/Admin/Users/Addusers.jsx";
import OrederinTake from "./component/Employer/orderinTake/OrederinTake.jsx";
import OrderScan from "./component/Employer/orderinTake/OrderScan.jsx";
import Pretreatnew from "./component/Employer/Pretreat/Pretreatnew.jsx";
import Dtg from "./component/Employer/Dtg/Dtg.jsx";
import Qcproblem from "./component/Employer/Qc/QcProblem.jsx";
import Qc from "./component/Employer/Qc/Qc.jsx";
import Shiping from "./component/Employer/Shiping/Shiping.jsx";
import AddProduct from "./component/Employer/ProductMangment/AddProduct";
import Login from "./component/login/Login.jsx";
import ProductMangment from "./component/Employer/ProductMangment/ProductMangment.jsx";
import EditProduct from "./component/Employer/ProductMangment/EditProduct";

function App() {
  const admin = localStorage.getItem("is_admin") == 0;
  const user = localStorage.getItem("is_admin") == 1;



  

  return (
    <>
      <Routes>
        <Route path="/" element={<Login />} />
        <Route element={<PrivateRoutesadmin />}>
          <Route path="/admin" element={admin ? <Navigate to="/" replace /> : <PageMain />}>
            <Route path="/admin/users/:id" exact element={<Users />} />
            <Route path="/admin/users/adduser" exact element={<Addusers />} />
            <Route path="/admin/overview/:id" exact element={<Overview />} />
            <Route path="/admin/pretreat/:id" exact element={<Pretreat />} />
          </Route>

          <Route path="/user" element={user ? <Navigate to="/" replace /> : <PagemainEmployer />}>
            <Route path="/user/orderintake/:id" exact element={<OrederinTake />} />
            <Route path="/user/orderintake/Orderscan" exact element={<OrderScan />} />
            <Route path="/user/pretreat/:id" exact element={<Pretreatnew />} />
            <Route path="/user/dtg/:id" exact element={<Dtg />} />
            <Route path="/user/qc/:id" exact element={<Qc />} />
            <Route path="/user/qc/qcproblem" exact element={<Qcproblem />} />
            <Route path="/user/productmangment/:id" exact element={<ProductMangment />} />
            <Route path="/user/EditProduct/:id" exact element={<EditProduct/>} />
            <Route path="/user/addprodut/:id" exact element={<AddProduct />} />
            <Route path="/user/shiping/:id" exact element={<Shiping />} />
          </Route>
        </Route>
      </Routes>
    </>
  );
}

export default App;
