import React, { useState } from "react";
import useStyles from "./StyleMenu.js";

import Typography from "@mui/material/Typography";
import Grid from "@mui/material/Grid";

import PieChartIcon from "@mui/icons-material/PieChart";
import PersonIcon from "@mui/icons-material/Person";
import ColorLensIcon from "@mui/icons-material/ColorLens";
import { NavLink } from "react-router-dom";
import logo from "./logo.png";
import { Button } from "@mui/material";
import LogoutIcon from '@mui/icons-material/Logout';
import { useNavigate } from "react-router-dom";

export default function Menu() {
  const [anchorEl, setAnchorEl] = React.useState(null);
  let navigate = useNavigate();
  const handleClose = () => {
    setAnchorEl(null);
    localStorage.clear();
    navigate("/", { replace: true });
    window.location.reload();
  };


  const classes = useStyles();

  const [bgColour, setBgColour] = useState("#363740");
  const [borderleft, setborderleft] = useState("");
  const appStyles = {
    height: "46px",
    width: "100%",
    background: `${bgColour}`,
    borderLeft: `${borderleft}`,
  };

  const [bgColour1, setBgColour1] = useState("#363740");
  const [borderleft1, setborderleft1] = useState("");
  const appStyles1 = {
    height: "46px",
    alignItems: "center",
    width: "100%",
    background: `${bgColour1}`,
    borderLeft: `${borderleft1}`,
  };

  const [bgColour2, setBgColour2] = useState("#363740");
  const [borderleft2, setborderleft2] = useState("");
  const appStyles2 = {
    height: "46px",
    width: "100%",
    alignItems: "center",
    background: `${bgColour2}`,
    borderLeft: `${borderleft2}`,
  };
  const [bgColour3, setBgColour3] = useState("#363740");
  const [borderleft3, setborderleft3] = useState("");
  const appStyles3 = {
    height: "46px",
    width: "100%",
    position:"absolute",
    bottom:"1.3em",
    display:"flex",
    alignItems: "center",
    background: `${bgColour3}`,
    borderLeft: `${borderleft3}`,
  };

  

  return (
    <div className={classes.menu}>
      <Grid container ditection={"row"} alignItems={"center"} mt={10}>
        <Grid item>
          <NavLink to="overview/Overview">
            <img src={logo} className={classes.logoicon} alt="logo" />
          </NavLink>
        </Grid>
        <Grid item>
          <NavLink to="overview/Overview">
            <Typography variant="h6" className={classes.logotext}>
              DTG auto system
            </Typography>
          </NavLink>
        </Grid>
      </Grid>

      <Grid container alignItems={"center"} className={classes.itemmenu} mt={10}>
        <Grid
          container
          item
          mb={4}
          className={classes.itemlink}
          style={appStyles}
          onMouseEnter={() => {
            setBgColour("rgb(159, 162, 180,0.08)");
            setborderleft("2px solid white");
          }}
          onMouseLeave={() => {
            setBgColour("#363740");
            setborderleft("");
          }}
        >
          <Grid item alignItems={"center"}>
            <NavLink to="overview/Overview">
              <PieChartIcon className={classes.icon} />
            </NavLink>
          </Grid>

          <Grid item>
            <NavLink to="overview/Overview" className={classes.itemtext} style={({ isActive }) => ({ color: isActive ? "#D9D9D9" : "rgb(159,162,180,0.40)" })}>
              Overview
            </NavLink>
          </Grid>
        </Grid>

        <Grid
          container
          item
          mb={3}
          className={classes.itemlink}
          style={appStyles1}
          onMouseEnter={() => {
            setBgColour1("rgb(159, 162, 180,0.08)");
            setborderleft1("2px solid white");
          }}
          onMouseLeave={() => {
            setBgColour1("#363740");
            setborderleft1("");
          }}
        >
          <Grid item alignItems={"center"}>
            <NavLink to={"users/Users"}>
              <PersonIcon className={classes.icon} />
            </NavLink>
          </Grid>
          <Grid item>
            <NavLink to="users/Users" className={classes.itemtext} style={({ isActive }) => ({ color: isActive ? "#D9D9D9" : "rgb(159,162,180,0.40)" })}>
              Users
            </NavLink>
          </Grid>
        </Grid>

        <Grid
          container
          item
          mb={3}
          className={classes.itemlink}
          style={appStyles2}
          onMouseEnter={() => {
            setBgColour2("rgb(159, 162, 180,0.08)");
            setborderleft2("2px solid white");
          }}
          onMouseLeave={() => {
            setBgColour2("#363740");
            setborderleft2("");
          }}
        >
          <Grid item alignItems={"center"}>
            <NavLink to={"pretreat/Pre treat"}>
              <ColorLensIcon className={classes.icon} />
            </NavLink>
          </Grid>
          <Grid item>
            <NavLink to="pretreat/Pre-treat" className={classes.itemtext} style={({ isActive }) => ({ color: isActive ? "#D9D9D9" : "rgb(159,162,180,0.40)" })}>
              Pre treat
            </NavLink>
          </Grid>
        </Grid>
      </Grid>

      <Grid
          container
          item
          mb={3}
          className={classes.itemlink}
          style={appStyles3}
          onMouseEnter={() => {
            setBgColour3("rgb(159, 162, 180,0.08)");
            setborderleft3("2px solid white");
          }}
          onMouseLeave={() => {
            setBgColour3("#363740");
            setborderleft3("");
          }}
        >
          <Button onClick={handleClose}>

          <Grid item alignItems={"center"}>
            <NavLink to={"/"}>
              <LogoutIcon className={classes.iconlogout} />
            </NavLink>
          </Grid>
          <Grid item>
            <NavLink to="/" className={classes.itemtextlogout} style={({ isActive }) => ({ color: isActive ? "#D9D9D9" : "rgb(159,162,180,0.40)" })}>
              Log out
            </NavLink>
          </Grid>
          </Button>
        </Grid>

    </div>
  );
}
