import React from "react";

import { Button, Grid, Typography } from "@mui/material";

import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import { Box } from "@mui/system";
import barcode from '../img/barcode.png'
import Tshert from "../img/Tshert.png"

function createData(name, calories, fat, carbs, protein) {
	return { name, calories, fat, carbs, protein };
}

const rows = [
	createData(
		1234567,
		"Black",
		"XL",
		"F",
		<img src={Tshert} style={{ width: "10%", height: "10%" }} alt="Tshert"/>
	),
];

export default function OrderScan() {
	return (
		<div style={{ marginLeft: "2rem" }}>
			<Grid
				container
				spacing={2}
				
			>
				<Grid item xs={3} md={3}>
					<Typography>Scan barcode</Typography>
				</Grid>
			</Grid>
			<Grid container spacing={2}>
				<Grid item xs={3} md={3}>
					<img src={barcode} style={{ width: "140%",marginLeft:"-1em",marginTop:"-1em" }} alt="barcode" />
				</Grid>
				<Grid
					item
					xs={3}
					md={3}
					style={{ marginTop: "7rem", marginLeft: "6rem" }}
				>
					<Button variant="contained" color="success">
						{" "}
						scan it
					</Button>
				</Grid>
				<Grid item xs={3} md={3}>
				<img src={Tshert} style={{ marginLeft:"1.2em",width: "110%" ,"zIndex":50}}  alt="Tshert" />
				</Grid>
			</Grid>
			<Grid
				container
				spacing={2}
				style={{ marginBottom: "5.5rem",marginTop:"-8em" }}
			>
				<Grid item xs={3} md={3}>
					<Box sx={{ width: "152px", height: "48px", border: "thin solid gray" }}>

					<Typography sx={{ margin: "12px" }}> 1322526</Typography>
					</Box>
				</Grid>
			</Grid>
			<Grid
				container
				spacing={2}
				style={{ marginBottom: "1em", marginTop: "0.2rem" }}
			>
				<Grid item xs={3} md={3}>
					<Typography> barcod:1322526</Typography>
				</Grid>
			</Grid>
			<Grid
				container
				spacing={2}
				style={{ marginBottom: "1.5rem", marginTop: "0.2rem",marginLeft:"-2.5em" }}
			>
				<Grid item xs={10} md={10}>
					<TableContainer component={Paper}>
						<Table sx={{ minWidth: 700 }} aria-label="simple table">
							<TableHead>
								<TableRow>
									<TableCell sx={{width:100}}>Product Id</TableCell>
									<TableCell align="right">Color</TableCell>
									<TableCell align="right">Size</TableCell>
									<TableCell align="right">F/B</TableCell>
									<TableCell align="center">Preview</TableCell>
								</TableRow>
							</TableHead>
							<TableBody>
								{rows.map((row) => (
									<TableRow
										key={row.name}
										sx={{
											"&:last-child td, &:last-child th":
												{ border: 0 },
										}}
									>
										<TableCell component="th" scope="row">
											{row.name}
										</TableCell>
										<TableCell align="right">
											{row.calories}
										</TableCell>
										<TableCell align="right">
											{row.fat}
										</TableCell>
										<TableCell align="right">
											{row.carbs}
										</TableCell>
										<TableCell align="center"  style={{width:330}}>
											{row.protein}
										</TableCell>
									</TableRow>
								))}
							</TableBody>
						</Table>
					</TableContainer>
				</Grid>
			</Grid>
			<Grid
				container
				spacing={2}
				style={{ marginBottom: "1em" }}
			>
				<Grid item xs={3} md={3}>
					<Button variant="contained" color="success">
						{" "}
						submit
					</Button>
				</Grid>
			</Grid>
		</div>
	);
}
