import React, { useEffect, useState } from "react";
import axios from "axios";
import { format } from "date-fns";
import Paper from "@mui/material/Paper";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import Spinner from "../../Employer/Spinner";
import TableRow from "@mui/material/TableRow";

import Fab from "@mui/material/Fab";
import AddIcon from "@mui/icons-material/Add";

import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemText from "@mui/material/ListItemText";
import ListItemAvatar from "@mui/material/ListItemAvatar";
import { useParams } from "react-router-dom";
import { NavLink } from "react-router-dom";
import "./styleuser.css";
import img from "./img.png";
import InfoButton from "./InfoButton";

import SelectRol from "./SelectRol";

const columns = [
  { id: "name", label: "Username", minWidth: 100 },
  { id: "last_login", label: "Date", minWidth: 100, align: "center", format: (value) => value.toLocaleString("en-US") },
  {
    id: "role",
    label: "Role",
    minWidth: 100,
    align: "center",
  },
  {
    id: "info",
    label: "Info",
    minWidth: 100,
    align: "center",
  },
];

const User = (props) => {
  const [loading, Setloading] = useState(false); 
  const [rows1, setrows1] = useState([]);
  const [characters, setCharacters] = useState([]);
  const params = useParams();
  console.log(params);


  useEffect(() => {

    var config = {
      method: "GET",
      url: `http://api.machinedtg.com/users/`,
      headers: {
        Authorization: "Bearer " + localStorage.getItem("token"),
      },
    };
    Setloading(true);
    axios(config)
      .then((response) => {
        console.log(response);

        const user = response.data.users;
        setrows1(user);
        Setloading(false);
      })
      .catch((error) => {
        console.log(error);
        Setloading(false);
      });

      var config2 = {
        method: "GET",
        url: `http://api.machinedtg.com/roles/`,
        headers: {
          Authorization: "Bearer " + localStorage.getItem("token"),
        },
      };

      axios(config2)
      .then((response) => {
        console.log(response);
        const rol = response.data.roles;

        setCharacters(rol);
        Setloading(false);
      })
      .catch((error) => {
        console.log(error);
        Setloading(false);
      });
  }, []);

  return (
    <main>
          {loading ? (
      <Spinner />
    ) : (
      <>

      <div>
        <h3 className="maintitle">All users</h3>
        <Paper sx={{ width: "100%", overflow: "hidden" }}>
          <TableContainer sx={{ maxHeight: 440 }}>
            <Table stickyHeader aria-label="sticky table">
              <TableHead>
                <TableRow>
                  {columns.map((column) => (
                    <TableCell
                      key={column.id}
                      align={column.align}
                      style={{
                        minWidth: column.minWidth,
                      }}
                    >
                      {column.label}
                    </TableCell>
                  ))}
                </TableRow>
              </TableHead>
              <TableBody>
                {rows1.map((row) => {
                  return (
                    <TableRow hover role="checkbox" tabIndex={-1} key={row.id}>
                      <TableCell key={row.id} style={{ minWidth: 100 }}>
                        <List style={{ height: "2rem", marginTop: "-2.5rem" }}>
                          <ListItem>
                            <ListItemAvatar>
                              <img src={img} alt="avatar" className="imguser" />
                            </ListItemAvatar>
                            <ListItemText primary={row.name} />
                          </ListItem>
                        </List>
                      </TableCell>
                      <TableCell align="center" style={{ minWidth: 100 }}>
                        {format(new Date(row.last_login), "yyyy/MM/dd kk:mm:ss")}
                      </TableCell>
                      <TableCell align="center" style={{ minWidth: 100 }}>
                        <SelectRol roles={row.role_name} setId={row.id} setcolor={row.role_color} getrole={characters} />
                      </TableCell>
                      <TableCell align="center" style={{ minWidth: 100 }}>
                        <InfoButton setId={row.id} />
                      </TableCell>
                    </TableRow>
                  );
                })}
              </TableBody>
            </Table>
          </TableContainer>
        </Paper>

        {/* button add user */}

        <div className="useb">
          <NavLink to="/admin/users/adduser">
            <Fab color="primary" aria-label="add">
              <AddIcon />
            </Fab>
          </NavLink>
        </div>
      </div>
      </>
        )}
    </main>
  );
};

export default User;
