import React, { useState,useEffect } from "react";
import axios from "axios";

import "./overstyle.css";
import Spinner from "../../Employer/Spinner";
import Box from "@mui/material/Box";
import Collapse from "@mui/material/Collapse";
import IconButton from "@mui/material/IconButton";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";

import Paper from "@mui/material/Paper";
import KeyboardArrowDownIcon from "@mui/icons-material/KeyboardArrowDown";
import KeyboardArrowUpIcon from "@mui/icons-material/KeyboardArrowUp";
import FrontSteppers from "./FrontSteper";
// import BackSteppers from "./Backsteper";



function Row(props) {
    // const 	history = [ 
    //     {
    //         id:"1",
    //         date: <FrontSteppers/>,
        
    //     },
    //     {
    //         id:"",
    //         date: <BackSteppers/>,
            
    //     },
    // ],


	const { row } = props;
	const [open, setOpen] = useState(false);

	return (
		<React.Fragment >
			<TableRow sx={{ "& > *": { borderBottom: "unset" } }} >
				<TableCell>
					<IconButton
						aria-label="expand row"
						size="small"
						onClick={() => setOpen(!open)}
					>
				
						{open ? (
							<KeyboardArrowUpIcon />
						) : (
							<KeyboardArrowDownIcon />
						)}
					</IconButton>
				</TableCell>
				<TableCell  component="th" scope="row">
					{row.id}
				</TableCell>
				<TableCell component="th" scope="row">
					{row.name}
				</TableCell>
				<TableCell align="left">{row.color}</TableCell>
				<TableCell align="left">{row.size}</TableCell>
				
			</TableRow>
			<TableRow>
				<TableCell
					style={{ paddingBottom: 0, paddingTop: 0 }}
					colSpan={6}
				>
					<Collapse in={open} timeout="auto" unmountOnExit>
						<Box sx={{ margin: 1 }}>
					
							<Table size="small" aria-label="purchases">
						
								<TableBody>
								
										<TableRow >
											<TableCell
												component="th"
												scope="row"
											>
												<FrontSteppers/>
											</TableCell>
										
										
										</TableRow>
									
								</TableBody>
							</Table>
						</Box>
					</Collapse>
				</TableCell>
			</TableRow>
		</React.Fragment>
	);
}



// const [name,setname]=useState("")
// const [color,setcolor]=useState("")
// const [size,setsize]=useState("")
// const [id,setid]=useState("")


export default function TableAco2() {
	
	const [productall,setproductall]=useState([])
		
	const [loading, Setloading] = useState(false); 
	
	
	

	


	useEffect(() => {

		var config = {
			method: "GET",
			url: `http://api.machinedtg.com/products/page/1`,
			headers: {
			  Authorization: "Bearer " + localStorage.getItem("token"),
			},
		}
		Setloading(true);
			axios(config)
			.then((response) => {
				const Productitem=response.data.products
				setproductall(Productitem)
		
				
				
		

				Setloading(false);
			})
			.catch((error) => {
				console.log(error);
				Setloading(false);
			});
		
			



	}, []);

	

	return (
	
		
		<TableContainer component={Paper}  >

			<Table aria-label="collapsible table"   stickyHeader  >

			{loading ? (
			<Spinner />
		  ) : (
			<>
				<TableHead  >

					<TableRow  >
						<TableCell >info</TableCell>
						<TableCell  >Id</TableCell>
						<TableCell  > Name</TableCell>
						<TableCell align="left">Color</TableCell>
						<TableCell align="left">Size</TableCell>
						
					</TableRow>
				</TableHead>

		
				<TableBody>
		
					{productall.map((row) => (
						<Row key={row.id} row={row} />
					))}
				</TableBody>
				</>
				)}
			</Table>
				
		</TableContainer>
		
		
	);
}
