import React, { useState } from "react";
import axios from "axios";
import Box from "@mui/material/Box";
import IconButton from "@mui/material/IconButton";
import img from "./img.png";
import OutlinedInput from "@mui/material/OutlinedInput";
import InputLabel from "@mui/material/InputLabel";
import InputAdornment from "@mui/material/InputAdornment";

import FormControl from "@mui/material/FormControl";
import Spinner from "../../Employer/Spinner";
import Visibility from "@mui/icons-material/Visibility";
import VisibilityOff from "@mui/icons-material/VisibilityOff";
import Button from "@mui/material/Button";

const FormData = require("form-data");

const formData = new FormData();

export default function Addusers() {
  const [loading, Setloading] = useState(false);

  const [values, setValues] = React.useState({
    showPassword: false,
  });

  const handleClickShowPassword = () => {
    setValues({
      ...values,
      showPassword: !values.showPassword,
    });
  };

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };

  const [name, setname] = useState("");
  const [email, setemail] = useState("");
  const [password, setpassword] = useState("");
  const handleChange = (prop) => (event) => {
    setpassword({ ...values, [prop]: event.target.value });
  };

  const reset = () => {
    setname("");
    setemail("");
    setpassword("");
  };

  const AddUser = (event) => {
    event.preventDefault();
    const user = {
      name: name,
      email: email,
      password: password,
    };
    formData.append("name", user.name);
    formData.append("email", user.email);
    formData.append("password", user.password);
    console.log(user);

    var options = {
      method: 'post',
      url: `http://api.machinedtg.com/users/`,
      
      headers: {
        Authorization: "Bearer " + localStorage.getItem("token"),
      },
      data: formData
      
    };
    Setloading(true);
    axios(options)
      .then((response) => {
        console.log(response);
        console.log(2);
        Setloading(false);

        reset();
        alert("ok");
        // window.location.reload()
      })
      .catch((error) => {
        console.log(error);
        Setloading(false);
        alert("notok");
      });
  };

  return (
    <Box sx={{ marginLeft: "5rem" }}>
       {loading ? (
      <Spinner />
    ) : (
      <>

      <div>
        <FormControl sx={{ m: 1, width: "25ch" }} variant="outlined">
          <InputLabel htmlFor="outlined-adornment-picture">
            <img className="imgadd" src={img} alt="User name" />
          </InputLabel>
          <OutlinedInput
            id="outlined-adornment-picture"
            type="file"
            value={values.picture}
            onChange={handleChange("picture")}
            label="picture"
            sx={{ display: "none" }}
          />
        </FormControl>
      </div>

      <div>
        <FormControl sx={{ m: 1, width: "45ch", marginTop: "7rem" }} variant="outlined">
          <InputLabel htmlFor="outlined-adornment-username">username</InputLabel>
          <OutlinedInput id="outlined-adornment-username" type="text" value={name} onChange={(e) => setname(e.target.value)} label="username" />
        </FormControl>
      </div>

      <div>
        <FormControl sx={{ m: 1, width: "45ch", marginTop: "1.5rem" }} variant="outlined">
          <InputLabel htmlFor="outlined-adornment-email">email</InputLabel>
          <OutlinedInput id="outlined-adornment-email" type="email" value={email} onChange={(e) => setemail(e.target.value)} label="email" />
        </FormControl>
      </div>

      <div>
        <FormControl sx={{ m: 1, width: "45ch", marginTop: "1.5rem" }} variant="outlined">
          <InputLabel htmlFor="outlined-adornment-password">Password</InputLabel>
          <OutlinedInput
            id="outlined-adornment-password"
            type={values.showPassword ? "text" : "password"}
            value={password}
            onChange={(e) => setpassword(e.target.value)}
            endAdornment={
              <InputAdornment position="end">
                <IconButton aria-label="toggle password visibility" onClick={handleClickShowPassword} onMouseDown={handleMouseDownPassword} edge="end">
                  {values.showPassword ? <VisibilityOff /> : <Visibility />}
                </IconButton>
              </InputAdornment>
            }
            label="Password"
          />
        </FormControl>
      </div>
     
      <Button variant="contained" sx={{ m: 1, width: "15ch", marginTop: "1rem" }} type="submit" onClick={AddUser}>
        Save
      </Button>
      </>
        )}

    </Box>
  );
}
